<script>
  import JaggedHr from '../components/Elements/JaggedHr.vue'

  export default {
    components: {
      JaggedHr
    },
    props: { title: 'About Me...' },
    created () {
      document.title = 'About Me | Andrew Finney Weilbaecher'
    },
    data () {
      const data = {
        transitionName: 'fade2'
      }
      return data
    }
  }
</script>

<template>

<div>
  <header class="site-header">
   <router-link to="/" exact class="logo"><div class="logo-holder"></div></router-link>
    <div class="page-title-top" :transition="transitionName">About Me...</div>
  </header>
  <div class="container single">
<section class="rain"></section>

    <div class="posts">
      <article class="post">
        <h1 class="post-title">About Me</h1>
        <div class="post-meta">
          <span>Andrew Finney Weilbaecher</span>
        </div>
          <JaggedHr></JaggedHr>

        <div class="post-content">
          <p>  Ive been developing websites for over a decade. What Ive learned is that coding is like the weather, it will continue to change on and off for a long, long time...</p>
          <p>Lets share a few emails or a pint of coffee and discuss your next project.</p>
        </div>
      </article>
    </div>


  </div>


</div>

</template>

<style>
.fade2-enter-active, .fade2-leave-active {
  transition: opacity 5s ease;
    background-color: red;
}
.fade2-enter, .fade2-leave-to
/* .component-fade-leave-active for <2.1.8 */ {
  opacity: 0;
}

</style>


