import About from './pages/About.vue'
import Resume from './pages/Resume.vue'
import FoodMenus from './pages/FoodMenus.vue'
import Single from './pages/Single.vue'
import Work from './pages/Work.vue'
import Contact from './pages/Contact.vue'
import PostList from './components/Post/List.vue'

const createListView = (type) => {
  return {
    name: `${type}-posts-view`,
    preFetch (store) {
      return store.dispatch('getPosts')
    },
    render (h) {
      return h(PostList, { props: { type } })
    }
  }
}

export const routes = [
  { path: '/:page(\\d+)?', component: createListView('index') },
  { name: 'about', path: '/about', component: About },
  { name: 'resume', path: '/resume', component: Resume },
  { name: 'FoodMenus', path: '/menus', component: FoodMenus },
  { name: 'Work', path: '/work', component: Work },
  { name: 'Contact', path: '/Contact', component: Contact },
  { name: 'single', path: '/post/:slug', component: Single }
]



